
ul{
    /* text-align: center; */
    margin: 0;
    padding: 5px;
    list-style-image: none;
    
}

header li a{
    padding: 0px 10px 1px 10px;
}

header li {
    display:inline;
    padding-bottom: 1px;
    margin: 1px;
    margin-left: 10px;
    max-height: 10px;
    /* box-shadow: 0px 2px black; */
    box-shadow: 2px 2px 1px rgb(0, 97, 161);
    background-color: rgb(173, 218, 255);
}

header li:hover {
    background-color: white;
}

a {
    color: black;
    text-decoration-line: none;
    /* text-decoration-style: double; */
    /* text-decoration-color: rgba(9, 255, 1, 0.993); */
}


header {
    margin: 0;
    background-color: transparent;
}

main {
    padding: 10px;
}


header, footer{
    background: rgb(0,172,255);
    background: linear-gradient(14deg, rgba(0,172,255,1) 0%, rgba(80,222,255,1) 100%);
}

footer{
    text-align: center;
    position: fixed;
    width: 100%;
    bottom: 0;

}

p{
    margin:0;
}